/**
 * 	Content script for the search engine pages.
 *
 * 	This script is responsible for augmenting the SERP it executes on by adding our logo / rate alongside
 * 	all stores which are supported on our website.
 *
 * 	It follows the rules specified @ https://www.giveasyoulive.[tld]/api/v1/serp/rules
 *
 * 	How it works:
 * 	1. Loads the SERP rules when the extension is first loaded
 * 	2. When on a search engine page...
 * 		- Extracts all search engine result URLs, as well as keeping a copy of where these URLs live in the DOM
 * 		- Calls the API with the collected URLs checking if they're supported
 * 		- If the store is supported, we add our own elements to the page using the previously kept references.
 *
 * 	@author Daniel Moffat
 */

var styles = require('./styles/less/serp.less');

var { Message, MessageSubject } = require('./lib/shared/message.js');
var { SERP_BUTTON_TEXT, SERP_LINK_TEXT } = require('./lib/shared/messages.js');
var { 
	contentScriptInitialise,
	dom,
	forEach,
	insertAfter,
	isSerp,
	text,
	whenDocumentReady
} = require('./lib/shared/util.js');
var Config = require('./lib/config.development.js');
var runtime = require('./lib/web-ext/runtime.js');



// Allow this content script to be debugged using a query param.
var debugEnabled = window.location.href.indexOf('debug=true') > -1;

class SerpContentScript {
	constructor() {
		// The rules for this host, the rules specify which things should be extracted from the page.
		this.rules = null;

		// The host we're currently on
		this.host = window.location.hostname;

		// A list of extracted items, each entry has the following structure:
		// {
	    //  	"title": <title DOM element>,
	    //  	"visibleLink": <visible link DOM element>,
	    //  	"url": 'http://the-extracted-url.com'
		// }
		this.extracted = [];

		// Used to unescape HTML entities
		this.domParser = new DOMParser();

		this.bindEventHandlers();

		// Request the extraction rules from the background page.
		this.requestRules();
	}

	/**
	 * Listen for messages from the background page.
	 */
	bindEventHandlers() {
		runtime.onMessage.addListener(this.onMessage.bind(this));
	}

	/**
	 * Logs the extracted URLs / visible links / titles in a human-readable way
	 */
	log() {
		for (var i = 0; i < this.extracted.length; i++) {
			var { url, visibleLink, title } = this.extracted[i];
			console.log((i + 1), title.textContent, url);
		}
	}

	/**
	 * Requests the SERP extraction rules from the background page.
	 */
	requestRules() {
		runtime.sendMessage(new Message(MessageSubject.GET_SERP_RULES));
	}

	/**
	 * Fired when a messages is received from the background page.
	 */
	onMessage(message, sender, sendResponse) {
		var { payload, subject } = message;
		console.log('SERP content script received message', message);
		switch(subject) {
			case MessageSubject.GET_SERP_RULES_RESPONSE:
				this.onRulesReceived(message);
				break;
			case MessageSubject.CHECK_SERP_URLS_RESPONSE:
				this.onUrlsChecked(payload);
				break;
			default:
				console.warn('Unable to handle event - ' + subject);
				break;
		}
		return true;
	}

	/**
	 * Fired when the rulesh have been received from the background page.
	 * @param A message whose 'payload' property contains the SERP rules.
	 */
	onRulesReceived(message) {
		// Extract the specific ruleset for this host from the message (i.e. On Google, pull out the Google rules, on Bing, the bing rules etc.)
		this.rules = message.payload[this.host] || null;
		if(this.rules === null) {
			console.log('No rules found for host:', this.host);
			return;
		}


		this.addSerpClassToBodyElement();

		var extractDelayMs = (this.rules.serpProvider === 'YAHOO' ? 100 : this.rules.serpProvider === 'DUCKDUCKGO' ? 800 : 0);

		// After a delay, extract the URLs from the page and send them to the background page for analysis.
		// Note: Yahoo change their URLs on page load, so a small delay has been added to ensure the URLs have been changed
		// before this script runs, otherwise we end up with a bunch of URLs we cannot process.
		setTimeout(function() {

			// Using the ruleset, extract the URLs from the page
			if(!this.extract()) {
				if(debugEnabled) {
					console.log('Failed to extract URLs from SERP', this.rules, this.host)
				}
				return;
			}

			// Send another message to the background page with the URLs we just extracted
			var payload = this.extracted.map(function(e) { return {url: e.url} });
			runtime.sendMessage(new Message(MessageSubject.CHECK_SERP_URLS, payload));

		}.bind(this), extractDelayMs)
	}

	addSerpClassToBodyElement() {
		document.body.classList.add('gayl-serp-' + this.rules.serpProvider);
	}

	/**
	 * Fired when the extracted URLs have been checked by the background page.
	 */
	onUrlsChecked(urls) {
		this.injectGlobalCss();
		this.injectUi(urls);
	}

	/**
	 * Uses a ruleset to extract metadata from the page.
	 * @return True if something was extracted, or false if nothing was extracted
	 */
	extract() {
		console.log('Extracting URLs from:', this.host, 'using rules:', this.rules);

		// First select the result elements
		var containers = document.querySelectorAll(this.rules.containerSelector);

		var self = this;

		// For each one, pull out the data we need
		forEach(containers, function(searchResult) {

			// These selectors are relative to searchResult, so can be very broad
			var donationRateTarget = searchResult.querySelector(self.rules.donationRateSelector);
			var title = searchResult.querySelector(self.rules.titleSelector);
			var visibleLink = searchResult.querySelector(self.rules.visibleLinkSelector);
			var url = searchResult.querySelector(self.rules.urlSelector);

            // Only process items that have a title and visible link.
            if(title && visibleLink) {

                highlight(url, '#ff919154');
                highlight(visibleLink, '#91ffb454');
                highlight(title, '#ff919154');
                highlight(searchResult, '#ccc');

                self.extracted.push({
                    container: searchResult,
                    title: title,
                    visibleLink: visibleLink,
                    url: url.href,
                    urlContainer: url,
                    donationRateTarget: donationRateTarget
                });
            }
		});

		if(debugEnabled) {
			console.log('Extracted:');
			this.log();
		}

		// Only return true if we actually found URLs, there's no point calling the API otherwise.
		return this.extracted.length > 0;
	}

	injectGlobalCss() {
		var styleEl = dom('style');
		styleEl.type = 'text/css';
		styleEl.appendChild(text(this.rules.additionalCss));
		document.head.appendChild(styleEl);
	}

	/**
	 *	@param urls, a list of URLs + their associated store / merchant object. Structure looks like this:
	 *	[
	 *		{
	 *			url: "the url",
	 *			merchant: {
	 *				...
	 *			}
	 *		}
	 *	]
	 */
	injectUi(urls) {
		for(var i = 0; i < urls.length; i++) {
			var response = urls[i];
			var previouslyExtracted = this.extracted[i];
			var store = response.merchant;

			// Grab the DOM elements we extracted previously to insert the GAYL stuff
			var title = previouslyExtracted.title;
			var visibleLink = previouslyExtracted.visibleLink;
			var resultContainer = previouslyExtracted.container;
			var urlContainer = previouslyExtracted.urlContainer;
			var donationRateTarget = previouslyExtracted.donationRateTarget;

			// Bail out if we couldn't match an item from the response to something we previously extracted.
			if(!title || !visibleLink) {
				return;
			}

			// Are we allowed to inject for this store?
			var canInject = store && store.notification === 'Y';

			if(canInject) {
				this.injectLogoAndStoreRate(donationRateTarget, store);
				this.injectTurnOnButton(resultContainer, store);
			}
		}
	}

	/**
	 * Injects the logo/store rate elements into the page.
	 */
	injectLogoAndStoreRate(donationRateTarget, store) {
		var logo = dom('span', 'gayl-logo');
		var rate = dom('span', 'gayl-store-rate');
		var rateText = SERP_LINK_TEXT.replace('$$maxStoreRate$$', this.getStoreRateText(store));
		rate.appendChild(text(rateText));

		// MUST be an 'a' element (or anything not a div/span) because bing have overridden document.createElement which
		// causes some strange behaviour where the element never gets appended to the document...spent over an hour trying to
		// figure out why and work around it, but couldn't figure it out.
		var container = dom('a', 'gayl-container')
		container.href = store.clickthroughUrl;
		container.appendChild(logo);
		container.appendChild(rate);

		insertAfter(container, donationRateTarget);
	}

	/**
	 * @param  resultContainer The individual search engine result container
	 * @param  store The retailer being injected
	 */
	injectTurnOnButton(resultContainer, store) {
		insertAfter(this.createButton(store), resultContainer);
	}

	/**
	 * Given a store object, will return the rate. This is only necessary because Â£ is sent through as &pound;
	 */
	getStoreRateText(store) {
		if(!store || !store.max) {
			return;
		}
		return this.domParser.parseFromString(store.max, 'text/html').documentElement.textContent;
	}

	createButton(store) {
		if(!store || !store.max) {
			return;
		}
		var buttonText = SERP_BUTTON_TEXT.replace('$$maxStoreRate$$', this.getStoreRateText(store));
		var button = dom('a', ['btn', 'btn-success', 'gayl-button'], buttonText);
		button.href = store.clickthroughUrl;

		return button;
	}
}

function highlight(element, colour) {
	if(debugEnabled && element != null && element.style) {
		element.style.backgroundColor = colour;
	}
}

// Chrome / FF / Edge execute the content script at 'document_end', which is defined as:
// "Scripts are injected immediately after the DOM is complete, but before subresources like images and frames have loaded."
// but Safari doesn't have this mechanism, so we have to manually check ourselves.
whenDocumentReady(function() {
	contentScriptInitialise(SerpContentScript, isSerp(window.location.href));
});